import { useEffect } from 'react';
import Hero from '../component/hero/hero';
import RestaurantsBarBody from '../component/restaurants-bar-body/restaurants-bar-body';

function RestaurantsBars() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  return (
    <div>
      <Hero
        images={[
          {
            url: '/images/hotel/restaurant-1.jpg',
          },
          {
            url: '/images/hotel/restaurant-2.jpg',
          },
          {
            url: '/images/hotel/restaurant-3.jpg',
          },
        ]}
      />
      <RestaurantsBarBody />
    </div>
  );
}

export default RestaurantsBars;
