import { Col, Container, Row } from 'react-bootstrap';

import './restaurants-bar-body.css';

const highlights = [
  'Todo Incluido 24 horas',
  'Todas las comidas y meriendas',
  'Desayuno: buffet de desayuno americano completo y estaciones de cocina en vivo',
  'Almuerzo: Buffet frío y caliente y estaciones de cocina en vivo',
  'Cena: Buffets y platos preparados en directo',
  'Restaurante Gourmet "Kulinarium" (a la carta)',
  'Restaurante asiático "Kawachi" (a la carta)',
  'asador (bufé)',
  'Aperitivos las 24 horas',
];

function RestaurantsBarBody() {
  return (
    <div className="restaurants-bar-body-div pt-5 pb-5">
      <Container>
        <Row className="justify-content-md-center text-center mb-5">
          <Col lg="8">
            <h1>Restaurantes y bares</h1>
          </Col>
        </Row>
        <Row className="justify-content-md-center text-left mb-5">
          <Col lg="10">
            <ul className="default-list">
              {highlights.map((value, i) => {
                return (
                  <li key={i}>
                    <i className="fa fa-check-circle"></i> {value}
                  </li>
                );
              })}
            </ul>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default RestaurantsBarBody;
